import request from 'utils/request';
/**
 * 
 * @param type 
 */
export const gettxcount = (type:string) => {
  const opts = {
    method:'gettxcount',
    params:[
      type
    ],
    baseUrl:'common'
  }
  return request(opts);
}

/**
 * 
 * @param size 
 * @param page 
 * @param type 
 */
export const getrawtransactions = (size:number,page:number,type:string) => {
  const opts = {
    method:'getrawtransactions',
    params:[
      size,
      page,
      type
    ],
    baseUrl:'common'
  }
  return request(opts);
}

/**
 * 
 * @param txid id
 */
export const getrawtransaction = (txid:string) => {
  const opts = {
    method:'getrawtransaction',
    params:[
      txid
    ],
    baseUrl:'common'
  }
  return request(opts);
}
/**
 * 
 * @param index 
 */
export const getblock = (index:number) => {
  const opts = {
    method:'getblock',
    params:[
      index
    ],
    baseUrl:'common'
  }
  return request(opts);
}


